.back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 30px;
  background: rgb(118, 118, 118);
  color: white;
  cursor: pointer;
  border-radius: 80px;
  border: none;
  box-shadow: 0 5px 5px #ccc;
  height: 40px;
}

.back-to-top:hover {
